<template>
  <Lay :route="route" :arr="arr" :url="url"/>
</template>

<script>
  import Lay from '@/components/common/template';
  import {newprocess} from '@/config/technical'
  export default {
    props: ["route"],
    data () {
      return {
        url: '/data/query/drillingtechdata',
        arr: newprocess
      }
    },

    components: {
      Lay
    },

    mounted: function() {
    
    },

    methods: {
    }
  }
</script>


