let drillingfluid =[
   {
     txt: '钻井液类型',
     prop: 'fluid_type'
   },
   {
     txt: '密度(g/cm³)',
     prop: 'density'
   },
   {
     txt: '漏斗粘度(s)',
     prop: 'viscosity'
   },
   {
     txt: '含砂量(%)',
     prop: 'sand_ratio'
   },
   {
     txt: '固含(%)',
     prop: 'solid_ratio'
   },
   {
     txt: 'pH值',
     prop: 'ph'
   },
   {
     txt: '滤饼厚度(mm)',
     prop: 'cake_thickness'
   },  
   {
     txt: '滤失量(mL)',
     child: [
       {
         txt: '常温',
         prop: 'fluid_loss'
       },
       {
         txt: '高温',
         prop: 'ht_fluid_loss'
       }
     ]
   },       
   {
     txt: '塑性粘度(mPa.s)',
     prop: 'plastic_viscosity'
   },
   {
     txt: '动切力(Pa)',
     prop: 'yield_value'
   },
   {
     txt: '流性指数n值',
     prop: 'nvalue'
   },
   {
     txt: '稠度系数k值(Pa.sn)',
     prop: 'kvalue'
   },
   {
     txt: '油/水比',
     prop: 'oil_water_ratio'
   },
   {
     txt: '破乳电压(v)',
     prop: 'emulsion_breaking_voltage'
   },
   {
     txt: '碱度(pom)(ml)',
     prop: 'alkalinity'
   }
 ]
 let newprocess =[
  {
    txt: '序号',
    prop: 'order_no'
  },
  {
    txt: '施工单位',
    prop: 'operate_unit'
  },
  {
    txt: '井眼尺寸(mm)',
    prop: 'borehole_size'
  },
  {
    txt: '新工艺新技术',
    prop: 'new_tech'
  },
  {
    txt: '起始井深(m)',
    prop: 'start_depth'
  },
  {
    txt: '结束井深(m)',
    prop: 'end_depth'
  },
  {
    txt: '进尺(m)',
    prop: 'footage'
  },
  {
    txt: '地层',
    prop: 'layer'
  },
  {
    txt: '钻头入井时间',
    prop: 'bitin_at'
  },
  {
    txt: '钻头出井时间',
    prop: 'bitout_at'
  },
  {
    txt: '入井趟数',
    prop: 'inwell_count'
  },
  {
    txt: '钻头数量(只)',
    prop: 'bit_count'
  },
  {
    txt: '螺杆数量(根)',
    prop: 'screw_count'
  },
  {
    txt: '纯钻时间(h)',
    prop: 'drilling_time'
  },
  {
    txt: '入井工作时间(d)',
    prop: 'work_time'
  },
  {
    txt: '机械钻速(m/h)',
    prop: 'penetrate_rate'
  },
  {
    txt: '行程速度(m/d)',
    prop: 'daily_footage'
  }
]

 export {
   drillingfluid,
   newprocess
 }


